import React, { useState, useEffect } from 'react';
import { 
  Layout, 
  Button, 
  Tag, 
  Space, 
  Row, 
  Col, 
  message,
  Tooltip,
  Typography
} from 'antd';
import { 
  CopyOutlined, 
  LogoutOutlined, 
  HomeOutlined,
  TeamOutlined
} from '@ant-design/icons';
import socketManager from '../utils/socket';
import { storage } from '../utils/storage';
import NumberBombGame from '../components/NumberBombGame';
import UserList from '../components/UserList';
import ChatPanel from '../components/ChatPanel';

const { Header, Content, Sider } = Layout;
const { Title } = Typography;

const RoomPage = () => {
  const [userInfo, setUserInfo] = useState(null);
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // 
    const storedUserInfo = storage.getUserInfo();
    if (!storedUserInfo) {
      message.error('');
      window.location.href = '/';
      return;
    }

    setUserInfo(storedUserInfo);
    
    // 
    socketManager.connect();
    
    // 
    socketManager.emit('reconnectToRoom', {
      roomId: storedUserInfo.roomId,
      userId: storedUserInfo.userId,
      nickname: storedUserInfo.nickname || ''
    });

    // 
    socketManager.on('roomJoined', (data) => {
      setLoading(false);
      const updatedUserInfo = {
        ...storedUserInfo,
        userId: data.userId,
        userNumber: data.userNumber,
        isHost: data.isHost
      };
      setUserInfo(updatedUserInfo);
      storage.setUserInfo(updatedUserInfo);
    });

    // 
    socketManager.on('userListUpdate', (userList) => {
      setUsers(userList);
    });

    // 
    socketManager.on('hostChanged', (data) => {
      message.info(`${data.newHostNickname} `);
      if (data.newHostId === userInfo?.userId) {
        const updatedUserInfo = { ...userInfo, isHost: true };
        setUserInfo(updatedUserInfo);
        storage.setUserInfo(updatedUserInfo);
      }
    });

    // 
    socketManager.on('error', (error) => {
      message.error(error);
      if (error.includes('') || error.includes('')) {
        storage.clearUserInfo();
        setTimeout(() => {
          window.location.href = '/';
        }, 2000);
      }
    });

    return () => {
      socketManager.off('roomJoined');
      socketManager.off('userListUpdate');
      socketManager.off('hostChanged');
      socketManager.off('error');
    };
  }, []);

  const copyRoomLink = () => {
    const link = `${window.location.origin}/?room=${userInfo.roomId}`;
    navigator.clipboard.writeText(link).then(() => {
      message.success('');
    }).catch(() => {
      message.error('');
    });
  };

  const leaveRoom = () => {
    socketManager.emit('leaveRoom');
    storage.clearUserInfo();
    window.location.href = '/';
  };

  const goHome = () => {
    window.location.href = '/';
  };

  if (loading || !userInfo) {
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <Content style={{ 
          display: 'flex', 
          alignItems: 'center', 
          justifyContent: 'center' 
        }}>
          <div>...</div>
        </Content>
      </Layout>
    );
  }

      return (
      <Layout style={{ minHeight: '100vh', background: '#f0f2f5' }}>
        <Header 
          style={{ 
            background: 'white',
            borderBottom: '1px solid #f0f0f0',
            padding: '16px 24px',
            height: 'auto',
            lineHeight: 'normal'
          }}
        >
          <div style={{ 
            display: 'flex', 
            alignItems: 'center', 
            justifyContent: 'space-between',
            minHeight: '48px'
          }}>
            <div>
              <div style={{ display: 'flex', alignItems: 'center', marginBottom: '4px' }}>
                <Title level={4} style={{ margin: 0, marginRight: '12px' }}></Title>
                <Tag color="blue" style={{ fontSize: '14px', padding: '4px 8px' }}>
                  {userInfo.roomId}
                </Tag>
              </div>
              <div style={{ fontSize: '12px', color: '#666' }}>
                <TeamOutlined style={{ marginRight: '4px' }} />
                : {users.length} 
              </div>
            </div>
            <Space>
              <Tooltip title="">
                <Button 
                  icon={<CopyOutlined />}
                  onClick={copyRoomLink}
                >
                  
                </Button>
              </Tooltip>
              <Tooltip title="">
                <Button 
                  icon={<HomeOutlined />}
                  onClick={goHome}
                >
                  
                </Button>
              </Tooltip>
              <Tooltip title="">
                <Button 
                  danger
                  icon={<LogoutOutlined />}
                  onClick={leaveRoom}
                >
                  
                </Button>
              </Tooltip>
            </Space>
          </div>
        </Header>

      <Layout style={{ background: '#f0f2f5' }}>
        <Content style={{ padding: '24px' }}>
          <Row gutter={24} style={{ height: '100%' }}>
            <Col xs={24} lg={16}>
              <NumberBombGame 
                userInfo={userInfo} 
                isHost={userInfo.isHost} 
              />
            </Col>
            
            <Col xs={24} lg={8}>
              <Space direction="vertical" style={{ width: '100%' }} size="large">
                <UserList 
                  users={users} 
                  playerCount={users.length}
                  currentUserId={userInfo.userId}
                />
                
                <ChatPanel 
                  userInfo={userInfo} 
                />
              </Space>
            </Col>
          </Row>
        </Content>
      </Layout>
    </Layout>
  );
};

export default RoomPage; 